@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900|Roboto+Slab:200,300,400,500,600,700,800,900&display=swap);
*{margin:0;padding:0;box-sizing:border-box}h1,h2,h3,h4,h5,h6{font-family:'Lato', sans-serif}header{padding:38px 0;border-bottom:1px solid lightgrey}header .wrapper{display:flex;width:1100px;align-items:baseline;margin:0 auto}header .logo{margin-right:2rem}header .logo a{color:#222;font-family:'Lato', sans-serif;font-weight:700;font-size:1.3rem;text-decoration:none}header .menu li{cursor:pointer;display:inline-block;margin:0 20px;color:#494949;border-bottom:2px solid #fff;-webkit-transition:color 0.3s ease-in-out;transition:color 0.3s ease-in-out;font-family:'Roboto Slab', serif;font-weight:300}header .menu li:hover{color:#222}header .menu li:first-of-type{border-color:grey}#filter-section{padding:30px 0;background:#eee;border-bottom:1px solid lightgrey}#filter-section .wrapper{width:1100px;margin:0 auto}#filter-section .wrapper .filter-search{margin-bottom:1.5rem}#filter-section .wrapper .filter-search form #search{color:black;font-size:1.1rem;font-family:'Roboto Slab', serif;font-weight:400;padding:20px;width:100%;border:none;outline:none;box-shadow:0px 7px 14px -10px rgba(0,0,0,0.5)}#filter-section .wrapper .filter-menu{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}#filter-section .wrapper .filter-menu .filter-box{position:relative;padding:20px;border:1px solid lightgrey;text-transform:capitalize;font-size:0.9rem;color:#7e7e7e;font-family:'Roboto Slab', serif;font-weight:300}#filter-section .wrapper .filter-menu .filter-box label{position:absolute;top:0;left:0;width:100%;height:100%;cursor:pointer}#filter-section .wrapper .filter-menu .filter-box input{position:absolute;opacity:0;top:0;left:0}#filter-section .wrapper .filter-menu .filter-box:hover{cursor:pointer;border-color:black}#filter-section .wrapper .filter-menu .filter-box i{margin-right:5px}#filter-section .wrapper .filter-menu .filter-box:nth-of-type(3) i{margin-right:0}#filter-section .wrapper .filter-menu .filter-box:nth-of-type(3) i:nth-of-type(2){margin-right:5px}#filter-section .wrapper .filter-menu-extra{margin-top:1.5rem;display:grid;grid-template-columns:1fr 1fr;grid-gap:10px}#filter-section .wrapper .filter-menu-extra .rooms{display:flex}#filter-section .wrapper .filter-menu-extra .rooms .pricetag{margin-left:20px}#filter-section .wrapper .filter-menu-extra .rooms label{display:block;margin-bottom:5px;font-family:'Roboto-Slab';font-weight:600}#filter-section .wrapper .filter-menu-extra .rooms select{padding:5px 2px}::-webkit-input-placeholder{color:#3d3d3d}:-ms-input-placeholder{color:#3d3d3d}::-ms-input-placeholder{color:#3d3d3d}::placeholder{color:#3d3d3d}#results{margin-top:5rem;margin-bottom:200px;font-family:'Roboto Slab', serif}#results .wrapper{width:1100px;margin:0 auto}#results .wrapper p{margin-bottom:14px;font-weight:300}#results .wrapper p span{font-weight:700}#results .wrapper select{margin-bottom:22px;padding:18px 10px;width:200px;font-size:1rem}#results .wrapper .results-grid{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:20px}#results .wrapper .results-grid .result{display:flex;flex-direction:column;height:400px;box-shadow:0px 4px 10px -5px rgba(0,0,0,0.5)}#results .wrapper .results-grid .result:hover{cursor:pointer}#results .wrapper .results-grid .result .image{height:216px;width:100%}#results .wrapper .results-grid .result .image .img{background-size:cover;background-repeat:no-repeat;background-position:center center;width:100%;height:100%}#results .wrapper .results-grid .result .info{color:#4d4d4d;padding:25px 25px 67px 25px}#results .wrapper .results-grid .result .info h5{margin-bottom:6px}#results .wrapper .results-grid .result .info h2{margin-bottom:20px}#results .wrapper .results-grid .result .info-facts{display:flex;justify-content:space-between;width:90%}#results .wrapper .results-grid .result .info-facts span i{margin-right:10px;align-self:center}#results .wrapper .results-grid .result .info-facts span:first-of-type{display:flex;align-items:center}#results .wrapper .pagination{margin-top:2.5rem;display:flex;justify-content:center}#results .wrapper .pagination div{font-size:0.9rem;margin:0 8px;cursor:pointer}#results .wrapper .pagination .page-num{border:1px solid lightgrey;padding:15px 20px}#results .wrapper .pagination .page-btn{border:1px solid lightgrey;padding:15px 20px;background:#3880be;color:#fff;-webkit-transition:0.2s background ease-in-out;transition:0.2s background ease-in-out}#results .wrapper .pagination .page-btn:hover{background:#2e6799}#footer{padding:3.5rem 0 6rem;background:#eee}#footer .wrapper{width:1100px;margin:0 auto}#footer .wrapper .footer-grid{display:grid;grid-template-columns:1fr 1fr 40%}#footer .wrapper .footer-grid .footer-contact h2{padding-bottom:15px;border-bottom:1px solid #b4b4b4;max-width:250px;color:#363636}#footer .wrapper .footer-grid .footer-contact ul{font-family:'Roboto Slab', serif;margin:1rem 0 0 0}#footer .wrapper .footer-grid .footer-contact ul li{list-style-type:none;margin:5px 0;font-size:15px}#footer .wrapper .footer-grid .footer-social h2{padding-bottom:15px;border-bottom:1px solid #b4b4b4;max-width:250px;color:#363636}#footer .wrapper .footer-grid .footer-social ul{font-family:'Roboto-Slab';margin:1rem 0 0 0;color:#363636}#footer .wrapper .footer-grid .footer-social ul li{display:inline-block;list-style-type:none;margin:0 5px}#footer .wrapper .footer-grid .footer-social ul li i{margin-right:10px;font-size:30px}#footer .wrapper .footer-grid .footer-social ul li i.fa-facebook{color:#3b5998}#footer .wrapper .footer-grid .footer-social ul li i.fa-twitter-square{color:#1da1f2}#footer .wrapper .footer-grid .footer-social ul li:first-of-type{margin-left:0}#footer .wrapper .footer-grid .footer-about h2{padding-bottom:15px;border-bottom:1px solid #b4b4b4;margin-bottom:15px;max-width:250px;color:#363636}#footer .wrapper .footer-grid .footer-about p{color:#363636;line-height:24px;font-size:15px;font-family:'Roboto Slab', serif}

